/* Define the colors */
$color-black: #111;
$color-red: #cc4b48;
$color-red2: #e4572e;
$color-vermilion: #f4442e;
$color-vermilion2: #d05b58;
$color-pink: #efb8ba;
$color-pink2: #e79598;
$color-pink4: #e69494;

$link-hover: #FFD09B;


// -------------- THEME SWITCHER -------------- //
// 颜色变量定义

//define colors - light and night

$color-mark-dark: #83a598;


// Dark theme colors
$bg-dark: #716457;
$text-dark: #F8F4E1;
$secondary-text-dark: rgba(241, 231, 209, 0.82);
$link-dark: #B6CBBD;
$visited-link-dark: #a359e9;
$color-blockquote-dark: #b5b5b5;
$bg-select-dark: rgba(152, 152, 152, 0.53);
$color-bg-code-dark: rgba(85, 76, 76, 0.47);
$color-point-dark: #819c81;
$w-dark: #fff;
$border-color-dark: rgba(251, 230, 189, 0.77);

// Light theme colors
$bg-light: #f9f7f1;
$text-light: #38383c;
$secondary-text-light: #666666;
$link-light: #1a89ae;
$visited-link-light: #921292;
$color-blockquote-light: #466060;
$bg-select-light: rgba(255, 239, 218, 0.6);
$color-bg-code-light: rgba(94, 89, 89, 0.13);
$color-point-light: #D45B587F;
$w-light: transparent;
$border-color-light: #38383c;
$color-mark-light: #b2d8d8;

// 主题样式定义
@mixin theme($bg-color, $w-color, $primary-text-color, $secondary-text-color, $link-color, $visited-link-color,  $blockquote-color, $color-bg-code, $bg-select,$color-point,$border-color,$color-mark) {
  background-color: $bg-color;
  color: $primary-text-color;
  a {
    color: $link-color;
    &:visited {
      color: $link-color;
    }
  }

  details {
    //border-left: 2px solid $primary-text-color;
  }

  details summary {
    color: $primary-text-color;
  }

  details[open] summary {
    border-bottom: 1px solid $primary-text-color;
  }

  // 改动：确保 pre 和 code 的背景颜色应用
  pre {
    background-color: $color-bg-code; // 确保背景颜色被应用
  }

  code:not(pre > code) {
    background-color: $color-bg-code; // 确保背景颜色被应用
    color: $primary-text-color;
  }

  *:target {
    background: transparent no-repeat;
    color: $primary-text-color;
  }

  table,
  th,
  td {
    border: thin solid $primary-text-color;
  }

  .toc {
    border: thin solid $primary-text-color;
    padding: 1rem;
  }

  figcaption {
    color: $secondary-text-color;
  }

  ::selection {
    background-color: $bg-select;
    /* 设置选中的背景颜色 */
  }

  blockquote {
    border-left: 2px solid $blockquote-color;
    color: $secondary-text-color;
  }


  mark {
    /* 移除默认的黄色背景 */
    background: linear-gradient(transparent calc(100% - 0.5em),
            $color-mark 0.5em) no-repeat;
    background-size: 100% 100%;
    color: inherit;
    /* 文字颜色继承父元素 */
    padding: 0 2px;
  }

  #header-post #nav a {
    color: $primary-text-color;
  }
  #header-post ul li {
    box-shadow: 2px 0 0 0 $primary-text-color;
  }
  .note__item {
    box-shadow: 4px 4px 0 0 $color-point;
  }
  .note__item {
    border: 2px solid $border-color;
  }

  .note__day {
    border-bottom: 2px solid $border-color;
  }
  ul li:before {
    color: $primary-text-color;
  }

  /* 主题切换按钮样式 */
  .theme-toggle {
    position: fixed;
    top: 20px;     /* 固定在页面顶部 */
    right: 10px;   /* 固定在页面右边 */
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 30px;   /* 控制按钮的宽度 */
    height: 10px;  /* 控制按钮的高度 */
    cursor: pointer;
    z-index: 1000; /* 确保按钮位于页面最上层 */
  }

  #light-btn, #dark-btn {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    transition: all 0.3s ease; /* 平滑过渡 */
  }

  /* Light 模式下按钮样式 */
  #light-btn {
    background-color: #F8F4E1;
    border: 2px solid #38383c; /* Dark 模式时的边框颜色 */
  }

  /* Dark 模式下按钮样式 */
  #dark-btn {
    background-color: #605151;
    display: none; /* 初始隐藏 */
    border: 2px solid #F8F4E1; /* Light 模式时的边框颜色 */
  }

  /* 切换到 dark 模式时，切换按钮的显示状态 */
  body.dark-mode #light-btn {
    display: none;
  }

  body.dark-mode #dark-btn {
    display: block;
  }

  /* 按钮的鼠标悬浮效果 */
  #light-btn:hover, #dark-btn:hover {
    background-color: #ecd0af; /* Light 模式下按钮悬浮时的背景色 */
    border-color: #594c4c; /* Dark 模式下按钮悬浮时的边框颜色 */
  }

  /* Dark 模式下的按钮悬浮效果 */
  body.dark-mode #dark-btn:hover {
    background-color: #5a5a5a; /* Dark 模式下按钮悬浮时的背景色 */
    border-color: #f1f1f1; /* Light 模式下按钮悬浮时的边框颜色 */
  }

  .goods-info:hover {
    text-shadow: $link-hover 1px 0 10px;
  }

}


// Dark theme appearance
@mixin dark-appearance {
  @include theme($bg-dark, $w-dark, $text-dark, $secondary-text-dark, $link-dark, $visited-link-dark,  $color-blockquote-dark, $color-bg-code-dark,$bg-select-dark,$color-point-dark,$border-color-dark,$color-mark-dark);
}

// Light theme appearance
@mixin light-appearance {
  @include theme($bg-light, $w-light, $text-light, $secondary-text-light, $link-light, $visited-link-light,  $color-blockquote-light, $color-bg-code-light, $bg-select-light,$color-point-light,$border-color-light,$color-mark-light);
}

// 应用主题样式
body[a="dark"] {
  background-color: #6d695c;
  background-image:
          repeating-linear-gradient(120deg, rgba(255,255,255,.1), rgba(255,255,255,.1) 1px, transparent 1px, transparent 60px),
          repeating-linear-gradient(60deg, rgba(255,255,255,.1), rgba(255,255,255,.1) 1px, transparent 1px, transparent 60px),
          linear-gradient(60deg, rgba(0,0,0,.1) 25%, transparent 25%, transparent 75%, rgba(0,0,0,.1) 75%, rgba(0,0,0,.1)),
          linear-gradient(120deg, rgba(0,0,0,.1) 25%, transparent 25%, transparent 75%, rgba(0,0,0,.1) 75%, rgba(0,0,0,.1)) !important;
  background-size: 70px 120px;
  @include dark-appearance;
}

body[a="light"] {
  @include light-appearance;
  background-image: linear-gradient(#e4e4e4 1px, transparent 1px),
  linear-gradient(90deg, #e4e4e4 1px, transparent 1px);
  background-size: 10px 10px, 10px 10px, 2px 2px, 2px 2px;
}

@media (prefers-color-scheme: dark) {
  body[a="auto"] {
    @include dark-appearance;
  }
}

@media (prefers-color-scheme: light) {
  body[a="auto"] {
    @include light-appearance;
  }
}

