/* 页面整体容器，使用 flexbox 布局 */
.main-container {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  /* 内容与菜单之间的间距 */
}

/* 内容部分 */
.content {
  flex: 1;
  /* 内容部分占据剩余空间 */
}

/* 菜单部分，固定右侧 */
.menu-float {
  flex: 0 0 50%;
  /* 菜单部分占据 50% 宽度 */
  box-sizing: border-box;
}

/* 手机端适配：布局变为单列 */
@media (max-width: 768px) {
  .main-container {
    flex-direction: column;
    /* 改为垂直排列 */
    gap: 0;
  }

  .menu-float {
    width: 100%;
    /* 菜单占满宽度 */
    box-shadow: none;
    /* 去掉阴影 */
  }

  body {
    padding: 0 5px;
  }

  .w {
    max-width: 740px;
    margin: 0 auto;
    padding: 1rem 0.5rem;
  }

  .pre-next-btn {
    font-size: 3.5vw;
  }
}


