/* 整个滚动条 */
::-webkit-scrollbar {
  width: 12px;
  /* 设置滚动条的宽度（竖直滚动条） */
  height: 12px;
  /* 设置滚动条的高度（水平滚动条） */
}

/* 滚动条的轨道（背景） */
::-webkit-scrollbar-track {
  background: rgba(220, 213, 207, 0.66);
  /* 设置滚动条轨道的背景颜色 */
  border-radius: 10px;
  /* 设置轨道的圆角 */
}

/* 滚动条的滑块（可以拖动的部分） */
::-webkit-scrollbar-thumb {
  background: $color-mark-dark;
  /* 设置滑块的颜色 */
  border: 2px solid #f1f1f1;
  /* 可选：为滑块添加边框，与轨道的颜色匹配 */
}

/* 滚动条滑块被鼠标悬停时的样式 */
::-webkit-scrollbar-thumb:hover {
  background: #555;
  /* 鼠标悬停时滑块的颜色 */
}

/* 可选：滚动条角落的部分 */
::-webkit-scrollbar-corner {
  background: #f1f1f1;
  /* 设置滚动条角落的背景颜色 */
}


