  position: relative;
  text-decoration: none;
  &:after {
    position: absolute;
    left: 0;
    width: 100%;
    height: 20px;
    content: "";
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: repeat-x;
    background-position: center center;
    background-size: 110% 110%;
  }
}
